<template>
  <div class="select-answer">
    <ul>
      <!-- 등록, 수정 -->
      <li v-if="$route.meta.mode !== 'detail'">
        <div v-for="(item, index) in answerItems" :key="index">
          <input type="radio" disabled/>
          <input type="text" v-model="answerItems[index]"/>
          <input type="button" value="삭제" @click="removeAnswerItem(index)"/>
        </div>
        <div>
          <input type="radio" disabled/>
          <h3 @click="addAnswer">추가</h3>
        </div>
      </li>
      <!-- 상세 -->
      <li v-else>
        <div v-for="(item, index) in answerItems" :key="index">
          <input type="radio" disabled/>
          <input type="text" v-model="answerItems[index]" readonly/>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'SelectAnswer',
  props: ['vo'],
  data () {
    return {
      answerItems: []
    }
  },
  created () {
    // 수정, 상세 화면 일때
    if (this.$route.meta.mode !== 'register') this.answerItems = this.vo.answerItem
  },
  updated () {
    // 응답문항 셋팅 트리거 자식 -> 부모
    this.$parent.$emit('setAnswerItem', this.answerItems)
  },
  methods: {
    // 응답문항 추가
    addAnswer () {
      this.answerItems.push('')
    },
    // 응답문항 삭제
    removeAnswerItem (index) {
      this.answerItems.splice(index, 1)
    }
  }
}
</script>

<style scoped>
 div.select-answer {
   font-size: 15px;
   height: auto;
 }
 div.select-answer ul {
   list-style: none;
   padding: 10px 0px 10px 0px;
 }
 div.select-answer ul li div {
   margin: 10px 0px 10px 0px;
 }
 div.select-answer ul li div input[type="radio"] {
   position: relative;
   margin: 0px 0px 0px 0px;
   vertical-align: middle;
 }
 div.select-answer ul li div input[type="text"] {
   position: relative;
   margin: 0px 0px 0px 0px;
   vertical-align: middle;
   border: 0;
   padding: 10px 10px 10px 0px;
   width: 20em;
   top: -1px;
 }
 div.select-answer ul li div h3 {
   position: relative;
   left: 18px;
   top: -20px;
   margin: 0px;
 }
</style>



